import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import axios from 'axios';
import classNames from 'classnames';

class VideoLoader extends Component {

    container;
    titles = {};
    lyrics = {};
    players = {};
    onShowLyrics;
    playingIndex = null;

    constructor(props) {
        super(props);

        this.state = {
            videoIds: props.videoIds || [],
            players: []
        }
        //constructor(container, videoIds, params = {}, onShowLyrics) {
        //this.container = container;
        this.onShowLyrics = props.onShowLyrics;

        window.onYouTubePlayerAPIReady = this.onYouTubePlayerAPIReady;
        this.loadScript();
    }

    loadScript = () => {
        if (document.getElementById('youTubeScript')) {
            setTimeout(() => this.onYouTubePlayerAPIReady());
            return;
        }
        var script = document.createElement('script');
        script.src = 'https://www.youtube.com/iframe_api';
        script.id = 'youTubeScript';
        script.onload = this.onScriptLoaded;
        var firstScriptTag = document.getElementsByTagName('script')[0];
        firstScriptTag.parentNode.insertBefore(script, firstScriptTag);
    }

    onScriptLoaded = () => {

    }

    prev = () => {
        let p;
        if (this.playingIndex === null) {
            return;
        } else {
            p = this.state.players[this.playingIndex];
        }

        if (p.index == 0) {
            return;
        }

        p.player.stopVideo();
        p.isPlaying = false;

        const prevPlayer = this.state.players[this.playingIndex - 1]
        prevPlayer.player.playVideo();
        this.playingIndex = prevPlayer.index;

        this.players[this.playingIndex].scrollIntoView({ behavior: "smooth" })

        this.forceUpdate();
    }

    play = () => {
        let p;
        if (this.playingIndex === null) {
            p = this.state.players[0];
        } else {
            p = this.state.players.find(x => x.index === this.playingIndex);
        }

        p.player.playVideo();
        p.isPlaying = true;

        this.playingIndex = p.index;

        this.forceUpdate();
    }

    pause = () => {
        // let p;
        // if (this.playingIndex === null) {
        //     p = this.players[0];
        // } else {
        //     p = this.players[this.playingIndex];
        // }

        // p.player.pauseVideo();
        // p.isPlaying = false;
        // this.playingIndex = null;

        // this.forceUpdate();
    }

    stop = () => {
        let p;
        if (this.playingIndex === null) {
            p = this.players[0];
        } else {
            p = this.state.players.find(x => x.index === this.playingIndex);
        }

        p.player.stopVideo();
        p.isPlaying = false;
        this.playingIndex = null;

        this.forceUpdate();
    }

    next = () => {
        let p;
        if (this.playingIndex === null) {
            return;
        } else {
            p = this.state.players[this.playingIndex];
        }

        if (p.index >= this.state.players.length - 1) {
            return;
        }

        p.player.stopVideo();
        p.isPlaying = false;

        const nextPlayer = this.state.players[this.playingIndex + 1]
        nextPlayer.player.playVideo();

        this.playingIndex = nextPlayer.index;

        this.players[this.playingIndex].scrollIntoView({ behavior: "smooth" })

        this.forceUpdate();
    }


    getLyric = (title, index) => {
        axios.get(`https://cors-anywhere.herokuapp.com/https://api.musixmatch.com/ws/1.1/track.search?q_track_artist=${title}&page_size=1&page=1&s_track_rating=desc&apikey=658a9bd36458276ed04fd0f3546f2b9c`)
            .then(res => {
                try {
                    var shareUrl = res.data.message.body.track_list[0].track.track_share_url;
                    shareUrl = shareUrl.substring(0, shareUrl.indexOf("?"));
                    if (shareUrl) {
                        this.lyrics[index].style.textDecoration = "underline";
                        this.state.players.find(x => x.index === index).lyrics = shareUrl;
                    }
                }
                catch (error) {
                    console.error(`Error on ${title}`);
                }
            })
    }

    onYouTubePlayerAPIReady = () => {
        const that = this;
        this.state.videoIds.forEach((x, i) => {

            const onPlayerReady = (event, index) => {
                const title = event.target.playerInfo.videoData.title
                    .replace(/\[.*\]/g, '')
                    .replace(/\(.*\)/g, '')
                    .replace(/\"/g, '');
                that.titles[index].textContent = title;
                that.getLyric(title, index);
            }

            const onPlayerStateChange = (event, index) => {
                if (event.data == window.YT.PlayerState.PLAYING) {
                    const p = that.state.players.find(x => x.index === index);
                    p.isPlaying = true;
                    this.playingIndex = p.index;

                    const playing = that.state.players.find(x => x.index !== index && x.isPlaying);
                    if (playing) {
                        playing.player.stopVideo();
                        playing.isPlaying = false;
                    }

                    that.forceUpdate()
                }
            }

            var p = new window.YT.Player(`player${i}`, {
                height: '100%',
                width: 'auto',
                videoId: x,
                events: {
                    'onReady': (e) => onPlayerReady(e, i),
                    'onStateChange': (e) => onPlayerStateChange(e, i)
                },
                host: 'https://www.youtube.com'
            })

            this.setState({
                players: this.state.players.concat([{
                    index: i,
                    player: p,
                    isPlaying: false
                }])
            })
        });
    }

    showLyrics = (index) => {
        const l = this.state.players.find(x => x.index == index).lyrics;
        l && this.onShowLyrics(l)
    }

    renderPlayer = (player, index) => {
        const classes = classNames("vrc__playerContainer",
            { "vrc__playerContainer--playing": index === this.playingIndex }
        );
        return (
            <div className={classes} ref={r => this.players[index] = r} key={index}>
                <div className="vrc__playerWrapper" id={`player${index}`} />
                <div className="vrc__playerInfo">
                    <h3 ref={r => this.titles[index] = r} /><br />
                    <span ref={r => this.lyrics[index] = r} className="vrc__playerOpenLyrics" onClick={() => this.showLyrics(index)}>Lyrics</span>
                </div>
            </div>
        );
    }

    render() {
        const { title } = this.props;
        const { videoIds, players } = this.state;
        return (
            <div ref={r => this.chartsContainer = r} className='vrc__playersContainer'>
                {title && <h1>{title}</h1>}
                {videoIds.map((x, i) => this.renderPlayer(x, i))}
                <div className="vrc__playerControls">
                    <div className="vrc__controlBtnContainer">
                        <div className="vrc__controlBtn vrc__controlBtn--prev" onClick={this.prev}>Prev</div>
                        <div className="vrc__controlBtn vrc__controlBtn--play" onClick={this.play}>Play</div>
                        <div className="vrc__controlBtn vrc__controlBtn--pause" onClick={this.pause}>Pause</div>
                        <div className="vrc__controlBtn vrc__controlBtn--stop" onClick={this.stop}>Stop</div>
                        <div className="vrc__controlBtn vrc__controlBtn--next" onClick={this.next}>Next</div>
                    </div>
                </div>
            </div>
        )
    }
}

export default VideoLoader;