import React, { Component } from 'react';
import VideoLoder from '../components/videoLoader';
import LyricsModal from './lyricsModal';

const withVideoLoader = (meta, chart) => {
    return class extends Component {
        constructor(props) {
            super(props);
            this.state = {
                showLyricModal: false,
                lyricUrl: null
            };
        }

        onShowLyrics = (lyricUrl) => {
            this.setState({
                showLyricModal: true,
                lyricUrl
            })
        }

        onCloseLyrics = () => {
            this.setState({
                showLyricModal: false,
                lyricUrl: null
            });
        }

        render() {
            const { showLyricModal, lyricUrl } = this.state;
            return (
                <>
                    <h1>{meta.title}</h1>
                    <VideoLoder 
                        videoIds={chart}
                        onShowLyrics={this.onShowLyrics}
                        onCloseLyrics={this.onCloseLyrics}
                    />
                    {/* <div ref={r => this.chartsContainer = r} className='vrc__playersContainer' /> */}
                    {showLyricModal && <LyricsModal src={lyricUrl} onClose={this.onCloseLyrics} />}
                </>
            )
        }
    }
}

export default withVideoLoader;