import React from 'react';
import { Modal } from "react-bootstrap";

const LyricModal = ({ onClose, title, src }) => (
    <Modal show={true} onHide={onClose} className="vrc__lyricModal">
        <Modal.Header closeButton>
            <Modal.Title>{title}</Modal.Title>
        </Modal.Header>
        <Modal.Body><iframe src={src} className="vrc__lyricsIFrame" /></Modal.Body>
    </Modal>
)

export default LyricModal;